import React from "react";
import "./CircleSelector.css";

export default function CircleSelector(props) {
    // in order to call a function as a prop? you need to wrap that function in an arrow function
    // handlePegClick={() => props.handlePegClick(1)}
    // render(){
    return (
        // remember, in a FUNCTIONAL component, there IS NO STATE; therefore, there is no "this";
        // meaning that if you want to access props, you can do so directly via "props.whatever"
        <div className="CircleSelector">
            {props.circles.map((circle, idx) =>
                <button
                    onClick={() => props.handleClick(idx)}
                    className={props.selectedIdx === idx ? "selected" : ""}
                >
                    Select Circle {circle}
                </button>
            )}
        </div>
    )
}